<template>
    <div class="login">
      <img alt="Vue logo" src="../assets/logo.png">
      <div style="margin:1rem 15vw; border: 1px solid #000000;">
        {{ text }}
      </div>

        <footer>
            <hr style="width:500px; border:2px solid;">
            <p>Footer區塊</p>
            <p>{{ nowDate }}</p>
        </footer>
    </div>
  </template>
  
  <script>


  export default {
    name: 'LoginView',
    components: {

    },
    data() {
        return {
            text: '123',
            nowDate: '123',
        }
    },

    methods: {
        upNowDate() {
            var dt = new Date();
            var yyyy = dt.getFullYear();
            var MM = (dt.getMonth()+1).toString().padStart(2, '0');
            var dd = dt.getDate().toString().padStart(2, '0');
            var HH = dt.getHours().toString().padStart(2, '0');
            var mi = dt.getMinutes().toString().padStart(2, '0');
            var ss = dt.getSeconds().toString().padStart(2, '0');
            this.nowDate = `${yyyy}-${MM}-${dd} ${HH}:${mi}:${ss}`;
            console.log(this.nowDate);
        }
    },

    created: function() {
        var self = this;
        console.log(self);
        this.$axios.get('http://localhost:8081/Hello')
            .then(function(response) {
                // 处理成功情
                self.text = response.data;
                console.log("OK");
                console.log(response);
            })
            .catch(function(error) {
                // 处理错误情况
                console.log("error");
                console.log(error);
            });
    },

    mounted: function() {
        // 在Vue實例創建後，開始定時更新當前時間
        this.upNowDate();
        setInterval(this.upNowDate, 10000);
    }
  }
  </script>
  